import { globalConfigInterface } from "./interface";

export let code_toggle = {
  js: ``,
  css:
    `.toggle-container {
      border: solid;
  border-width: 2px;
  border-color: #000;
    }
  `
}

export let code_button = {
  js: ``,
  css:
    `.button-container {
      border: solid;
  border-width: 2px;
  border-color: #000;
    }
  `
}

export let code_range = {
  js: ``,
  css:
    `.button-container {
      border: solid;
  border-width: 2px;
  border-color: #000;
    }
  `
}


export let firmata_config: globalConfigInterface = {
  js: {
    global_variables: [],
    rendered_views: [],
    components: [],
    analogRead: [
      {
        variable: 'lulzz',
        pin: 2,
        interval: 1000
      }
    ],
    renderer_update_interval: 1000,
    setup: (() => { let nishkal; }).toString()
  },
  css:
    `display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  // background-color : #1e1e1e;
  // height: 100%;
  `
};

export let MTRButtonComponentInterfaceSample =
  `interface MTRButtonComponent {
  content?: string | number;
  color?: string;
  disabled?: boolean;
  block?: boolean;
  clear?: boolean;
  default?: boolean;
  full?: boolean;
  large?: boolean;
  outline?: boolean;
  round?: boolean;
  small?: boolean;
  strong?: boolean;
}`

export let GlobalConfigInterfaceSample =
  `interface GlobalConfigInterface {
  global_variables?: Array<globalVariableInterface | string>;
  components?: Array<johnnyFiveClassInterface>;
  rendered_views?: Array<renderedViewInterface>;
  analogRead?: Array<analogReadInterface>;
  setup?: Function | string;
  renderer_update_interval: number;
  }

  interface globalVariableInterface {
    variable: string;
    initialize?: any;
}

interface johnnyFiveClassInterface {
  class:
  "Accelerometer" | "Altimeter" | "Animation" | "Barometer"
  | "Board" | "Boards" | "Button" | "Compass" | "ESC" | "ESCs"
  | "Expander" | "Fn" | "GPS" | "Gyro" | "Hygrometer" | "IMU"
  | "IR.Reflect.Array" | "Joystick" | "Keypad" | "LCD" | "Led"
  | "Led.Digits" | "Led.Matrix" | "Led.RGB" | "Leds"
  | "Light" | "Motion" | "Motor" | "Motors" | "Multi" | "Piezo"
  | "Pin" | "Proximity" | "Relay" | "Relays" | "Sensor" | "Servo"
  | "Servos" | "ShiftRegister" | "Stepper" | "Switch" | "Thermometer"
  variable: string;
  arguments?: any;
  setup?: Function;
}

  interface renderedViewInterface {
    component: 'MtrToggleComponent' | 'MtrRangeComponent' | 'MtrButtonComponent';
    id: number;
    variable: string;
    name?: string;  //For users convenience
    description?: string  //For users convenience;
}

interface renderedViewInterface {
  component: 'MtrToggleComponent' | 'MtrRangeComponent' | 'MtrButtonComponent';
  id: number;
  variable: string;
  name?: string;  //For users convenience
  description?: string  //For users convenience;
}

interface analogReadInterface {
  variable: string;
  pin: number;
  interval: number;
}`


