import { Component, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'menu-bar',
  templateUrl: 'menu-bar.html'
})
export class MenuBarComponent {

  @Output() event: EventEmitter<string> = new EventEmitter();

  constructor() {

  }

  clicked(icon: string) {
    this.event.emit(icon);
  }
}



