import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SocketServiceProvider } from '../providers/socket-service/socket-service';
import { ElectronServiceProvider } from '../providers/electron-service/electron-service';
import { AuthenticationServiceProvider } from '../providers/authentication-service/authentication-service';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  // rootPage: any = this.platform.is('mobileweb') ? 'ConnectMobilePage' : 'LandingPage';
  rootPage: any = 'MenuPage';

  constructor(private platform: Platform, statusBar: StatusBar,
    splashScreen: SplashScreen, private ssp: SocketServiceProvider, private esp: ElectronServiceProvider) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
    });
    this.platform.is('mobileweb') ? null : this.ssp.connectToSocketServer('http://192.168.1.7');
  }

  clickedOnMenuBar(event: string) {
    switch (event) {
      case 'minus':
        this.esp.minimize();
        break;
      case 'maximize':
        this.esp.maximise();
        break;
      case 'close':
        this.esp.closeWindow();
        break;
    }
  }
}




