import { NgModule } from '@angular/core';
import { MtrToggleComponent } from './mtr-toggle/mtr-toggle';
import { IonicModule } from 'ionic-angular';
import { HoverComponent } from './hover/hover';
import { CodebarComponent } from './codebar/codebar';
import { StatusBarComponent } from './status-bar/status-bar';
import { TabsBarComponent } from './tabs-bar/tabs-bar';
import { ActivityBarComponent } from './activity-bar/activity-bar';
import { WidgetsComponent } from './widgets/widgets';
import { MtrButtonComponent } from './mtr-button/mtr-button';
import { MtrRangeComponent } from './mtr-range/mtr-range';
import { CodeEditorComponent } from './code-editor/code-editor';
import { CssInspectorComponent } from './css-inspector/css-inspector';
import { ConsoleComponent } from './console/console';
import { MenuBarComponent } from './menu-bar/menu-bar';

import { MatTooltipModule, MatMenuModule, MatSnackBarModule, MatTabsModule } from '@angular/material';

@NgModule({
    declarations: [
        MtrToggleComponent,
        HoverComponent,
        CodebarComponent,
        StatusBarComponent,
        TabsBarComponent,
        ActivityBarComponent,
        WidgetsComponent,
        MtrButtonComponent,
        MtrRangeComponent,
        CodeEditorComponent,
        CssInspectorComponent,
        ConsoleComponent,
        MenuBarComponent,],
    imports: [IonicModule,
        MatTooltipModule,
        MatMenuModule,
        MatTabsModule,
        MatSnackBarModule
    ],
    exports: [
        MtrToggleComponent,
        HoverComponent,
        CodebarComponent,
        StatusBarComponent,
        TabsBarComponent,
        ActivityBarComponent,
        WidgetsComponent,
        MtrButtonComponent,
        MtrRangeComponent,
        CodeEditorComponent,
        CssInspectorComponent,
        ConsoleComponent,
        MenuBarComponent,],
    entryComponents: [MtrToggleComponent, MtrButtonComponent, MtrRangeComponent, CodeEditorComponent]
})
export class ComponentsModule { }



