import { Component, EventEmitter, Output, ViewEncapsulation, Renderer2 } from '@angular/core';
import {  MatTooltipDefaultOptions } from '../../../node_modules/@angular/material';


export const myCustomTooltipDefaults: MatTooltipDefaultOptions = {
  showDelay: 0,
  hideDelay: 5000,
  touchendHideDelay: 1000,
};


@Component({
  selector: 'activity-bar',
  templateUrl: 'activity-bar.html',
  // providers: [
  //   { provide: MAT_TOOLTIP_DEFAULT_OPTIONS, useValue: myCustomTooltipDefaults }
  // ],
  encapsulation: ViewEncapsulation.None
})
export class ActivityBarComponent {

  @Output() newActivity = new EventEmitter();

  constructor() {  }

  changeMode() {
    this.newActivity.emit('changeMode');
  }

  connectToBoard() {
    this.newActivity.emit('connect');
  }

  initialize() {
    this.newActivity.emit('initialize');
  }

  globalConfig() {
    this.newActivity.emit('globalConfigJSON');
  }

  globalStyle(){
    this.newActivity.emit('globalConfigCSS');
  }

  goToDesignerPage() {
    this.newActivity.emit('goToDesignerPage');
  }
}



