import { Component } from '@angular/core';
import { componentDataInterface } from '../../utilities/interface';
import CodeMirror from 'codemirror';
import { css_beautify } from 'js-beautify';
import { code_button } from '../../utilities/code-samples';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { Events } from 'ionic-angular';

@Component({
  selector: 'mtr-button',
  templateUrl: 'mtr-button.html'
})
export class MtrButtonComponent {

  id: number = Date.now();
  component = this;
  data: componentDataInterface = {
    config: {
      id: this.id,
      component: 'MtrButtonComponent',
      css_class: `mtr-button-${this.id}`,
      function: `Mtr_Button_${this.id}`,
      variable: `mtr_button_${this.id}`
    },
    code: {
      js: new CodeMirror.Doc('', 'javascript'),
      css: new CodeMirror.Doc(css_beautify(code_button.css, { indent_size: 2 }), 'css'),
    },
    localData: {}
  }

  constructor(private events: Events, private gsp: GlobalServiceProvider) {

    this.data.localData = {
      content: this.data.config.variable,
      color: '#000000'
    }
  }

  ngAfterViewInit() {
    this.gsp.addCSS(this.data);
    this.events.publish('misc-components : global-service : add-component', this);
    this.events.subscribe('socket-service : misc-components : update-renderer', (closure: Object) => {
      this.data.localData = closure[`${this.data.config.variable}`];
      // console.log(closure[`${this.data.config.variable}`]);
    });
  }

  change() {
    this.events.publish('misc-components : socket-service : firmata', this.data.config.function, this.data.localData);
  }

  openTab(tab: any) {
    this.events.publish('misc-components : global-service : add-tab', tab);
  }

  // updateJS(variable: string) {
  //   if (variable != undefined) {
  //     let find = new RegExp(variable, 'g');
  //     console.log(find);
  //     let newJS = this.data.code.js.getValue().replace(find, `${this.data.config.variable}`);
  //     this.data.code.js.setValue(newJS);
  //   }
  // }
}



