import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';
import { } from 'electron';
import { contextMenu } from '../../utilities/electron-resources';
import { AppUpdater } from "electron-updater";

@Injectable()
export class ElectronServiceProvider {

  MenuItem: typeof Electron.MenuItem;
  Menu: typeof Electron.Menu;
  app: Electron.App;
  dialog: Electron.Dialog;
  BrowserWindow: typeof Electron.BrowserWindow;
  currentWebContents: Electron.WebContents;
  autoUpdater: AppUpdater;

  constructor(public platform: Platform) {
    this.platform.is('electron') ? this.injectElectron() : console.log('Cannot Inject Electron');
    console.log('Auto updater');

  }

  injectElectron() {
    console.log('Injecting Electron');
    const { Menu, MenuItem, app, dialog, BrowserWindow, getCurrentWebContents } = require('electron').remote;
    this.MenuItem = MenuItem;
    this.Menu = Menu;
    this.app = app;
    this.dialog = dialog;
    this.BrowserWindow = BrowserWindow;
    this.currentWebContents = getCurrentWebContents();
    // console.log(location.hostname, location.origin, location.href, location.host);
  }

  editorContextMenu() {
    if (this.platform.is('electron')) {
      const menu = this.Menu.buildFromTemplate(contextMenu);
      const win = this.BrowserWindow.fromWebContents(this.currentWebContents);
      menu.popup({
        window: win, callback: () => {
          console.log('Closing');
        }
      });
    } else {
      console.log('Sorry not electron');
    }
  }

  minimize() {
    this.platform.is('electron') ?
      this.BrowserWindow.getFocusedWindow().minimize() : null;
  }

  maximise() {
    if (this.platform.is('electron')) {
      if (this.BrowserWindow.getFocusedWindow().isMaximized()) {
        this.BrowserWindow.getFocusedWindow().unmaximize();
      } else {
        this.BrowserWindow.getFocusedWindow().maximize();
      }
    }
  }

  closeWindow() {
    this.platform.is('electron') ?
      this.app.quit() : null;
  }
}


