import { Component, ViewChild, ElementRef } from '@angular/core';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { consoleDataInterface } from '../../utilities/interface';
import { MatSnackBar } from '../../../node_modules/@angular/material';
import { DomController } from 'ionic-angular';

@Component({
  selector: 'console',
  templateUrl: 'console.html'
})
export class ConsoleComponent {

  data: consoleDataInterface['data'] = [];
  maps = {
    log: {
      color: '#000000',
      icon : 'return-right'
      // icon: 'fa-angle-double-right'
    },
    warn: {
      color: '#ffff9a',
      icon : 'warning'
      // icon: 'fa-warning'
    },
    error: {
      color: '#f53d3d',
      icon : 'close'
      // icon: 'fa-close'
    },
    right: {
      color: '#25d55f',
      icon : 'checkmark'
      // icon: 'fa-check'
    },
    indent: {
      color: '#000000',
      icon : 'arrow-dropright'
      // icon: 'fa-angle-right'
    }
  }

  constructor(private gsp: GlobalServiceProvider, private domCtrl: DomController) {
    this.data = this.gsp.consoleData.data;
  }

  @ViewChild('myDiv') myDivRef: ElementRef;
  @ViewChild('myDivHeader') myDivHeaderRef: ElementRef;
  @ViewChild('myContent') myContentRef: ElementRef;
  myDiv: HTMLElement;
  myDivHeader: HTMLElement;
  myContent: HTMLElement;

  ngAfterViewInit() {
    this.myDiv = this.myDivRef.nativeElement;
    this.myDivHeader = this.myDivHeaderRef.nativeElement;
    this.myContent = this.myContentRef.nativeElement;
    this.dragster();
    this.myDiv.style.top = this.gsp.consoleData.top + 'px';
    this.myDiv.style.height = -this.gsp.consoleData.top + 'px';
    this.myContent.scrollTop = this.myContent.scrollHeight;
  }

  minimizeConsole() {
    this.myDiv.style.height = '33.5px';
    this.myDiv.style.top = '-33.5px';
    this.gsp.consoleData.top = -33.5;
  }

  maximiseConsole() {
    this.myDiv.style.height = '200px';
    this.myDiv.style.top = '-200px';
    this.gsp.consoleData.top = -200;
    this.myDiv.scrollTop = this.myDiv.scrollHeight;
    this.myContent.scrollTop = this.myContent.scrollHeight;
  }

  clearConsole() {
    this.gsp.consoleData.data = [];
    this.data = [];
  }

  contextMenu(event) {
    event.preventDefault();
  }

  dragster() {
    let self = this;
    dragElement(this.myDiv);
    function dragElement(elmnt) {
      var pos2 = 0, pos4 = 0;
      self.myDivHeader.onmousedown = dragMouseDown;
      function dragMouseDown(e) {
        e = e || window.event;
        e.preventDefault();
        // get the mouse cursor position at startup:
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag;
      }

      function elementDrag(e) {
        e = e || window.event;
        e.preventDefault();
        // calculate the new cursor position:
        pos2 = pos4 - e.clientY;
        pos4 = e.clientY;
        // set the element's new position:

          if (pos2 > -36) {///Import/ant to prevent flicker
            elmnt.style.height = -(elmnt.offsetTop - pos2) + "px";
          }
          if ((elmnt.offsetTop - pos2) < -33.5 && (elmnt.offsetTop - pos2) > -500) {
            let pos = (elmnt.offsetTop - pos2);
            if (pos > -100) {
              self.minimizeConsole();
            } else {
              self.gsp.consoleData.top = (elmnt.offsetTop - pos2);
              elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
            }
          }
      }

      function closeDragElement() {
        /* stop moving when mouse button is released:*/
        document.onmouseup = null;
        document.onmousemove = null;
      }
    }
  }
}



