export const menuTemplateDev: Electron.MenuItemConstructorOptions[] = [{
  label: 'Options',
  submenu: [{
    label: 'Open Dev Tools',
    click() {
      console.log('Tatti');
    },
  },],
}, {
  label: 'View',
  submenu: [{
    label: 'View IP Address',
    click() {
      const { dialog } = require('electron').remote;
      dialog.showMessageBox({
        title: 'IP Address',
        message: 'Hello'
      });
    }
  }]
}];

export const contextMenu: Electron.MenuItemConstructorOptions[] = [
  { role: 'copy' },
  { role: 'paste' },
  { type: 'separator' },
  { role: 'undo' },
  { role: 'redo' },
]

export const template: Electron.MenuItemConstructorOptions[] = [
  {
    label: 'Edit',
    submenu: [
      { role: 'undo' },
      { role: 'redo' },
      { type: 'separator' },
      { role: 'cut' },
      { role: 'copy' },
      { role: 'paste' },
      { role: 'pasteandmatchstyle' },
      { role: 'delete' },
      { role: 'selectall' }
    ]
  },
  {
    label: 'View',
    submenu: [
      { role: 'reload' },
      { role: 'forcereload' },
      { role: 'toggledevtools' },
      { type: 'separator' },
      { role: 'resetzoom' },
      { role: 'zoomin' },
      { role: 'zoomout' },
      { type: 'separator' },
      { role: 'togglefullscreen' }
    ]
  },
  {
    role: 'window',
    submenu: [
      { role: 'minimize' },
      { role: 'close' }
    ]
  },
  {
    role: 'help',
    submenu: [
      {
        label: 'Learn More',
        click() { require('electron').shell.openExternal('https://electronjs.org') }
      }
    ]
  }
]


