import { Injectable } from '@angular/core';
import { globalConfigInterface, componentDataInterface, menuListInterface, tabDataInterface, consoleDataInterface } from '../../utilities/interface';
import { firmata_config } from '../../utilities/code-samples';
import { Events } from 'ionic-angular';
import CodeMirror from 'codemirror';
import { css_beautify, js_beautify } from 'js-beautify';
import { addClassFromDump, addCSSFromDump } from './global-service-dump';

@Injectable()
export class GlobalServiceProvider {

  global_config = {
    js: new CodeMirror.Doc(js_beautify(JSON.stringify(firmata_config.js), { indent_size: 2 }), 'application/ld+json'),
    css: new CodeMirror.Doc(css_beautify(firmata_config.css, { indent_size: 2 }), 'css'),
  }
  tabsArray: Array<tabDataInterface> = [];
  currentTab: tabDataInterface = {
    other: "globalConfigJSON",
    mode: "JSON"
  };
  designerComponents: Array<any> = [];
  comPortList: Array<any> = [];
  consoleData: consoleDataInterface = {
    top: -200,
    data: [],
  };
  logger = {
    log: (message: string, indent?: number, fullColor?: boolean) => {
      this.consoleData.data.push({ message: message, indent: indent, type: 'log', fullColor: fullColor, timestamp: indent ? 1 : Date.now() });
    },
    warn: (message: string, indent?: number, fullColor?: boolean) => {
      this.consoleData.data.push({ message: message, indent: indent, type: 'warn', fullColor: fullColor, timestamp: indent ? 1 : Date.now() });
    },
    error: (message: string, indent?: number, fullColor?: boolean) => {
      this.consoleData.data.push({ message: message, indent: indent, type: 'error', fullColor: fullColor, timestamp: indent ? 1 : Date.now() });
    },
    right: (message: string, indent?: number, fullColor?: boolean) => {
      this.consoleData.data.push({ message: message, indent: indent, type: 'right', fullColor: fullColor, timestamp: indent ? 1 : Date.now() });
    },
    indent: (message: string, indent?: number, fullColor?: boolean) => {
      this.consoleData.data.push({ message: message, indent: indent, type: 'indent', fullColor: fullColor, timestamp: indent ? 1 : Date.now() });
    }
  }

  constructor(private events: Events) {
    this.appEvents();
    this.logger.log('Initializing Global Service');
    this.logger.log('Initialized Global Service', 40);
    this.logger.error('Initialized Global Service', 40);
    this.logger.indent('Hello World', 40);
    this.logger.right('Hello World', 80);
    this.logger.indent('Hello World', 80);
    this.logger.log('Initializing Global Service');
    this.logger.log('Initialized Global Service', 40);
    this.logger.error('Initialized Global Service', 40);
    this.logger.indent('Hello World', 40);
    this.logger.right('Hello World', 80);
    this.logger.indent('Hello World', 80); this.logger.log('Initializing Global Service');
    this.logger.log('Initialized Global Service', 40);
    this.logger.error('Initialized Global Service', 40);
    this.logger.indent('Hello World', 40);
    this.logger.right('Hello World', 80);
    this.logger.indent('Hello World', 80); this.logger.log('Initializing Global Service');
    this.logger.log('Initialized Global Service', 40);
    this.logger.error('Initialized Global Service', 40);
    this.logger.indent('Hello World', 40);
    this.logger.right('Hello World', 80);
    this.logger.indent('Hello World', 80);

  }

  appEvents() {
    this.events.subscribe('misc-components : global-service : add-component', (component: any) => {
      let flag = this.designerComponents.filter((val) => { return val.data.config.id == component.data.config.id });
      if (flag.length == 0) {
        this.designerComponents.push(component);
        let data: componentDataInterface = component.data;
        let json: globalConfigInterface["js"] = JSON.parse(this.global_config.js.getValue());
        json.rendered_views.push({ component: data.config.component, id: data.config.id, variable: data.config.variable });
        this.global_config.js.setValue(js_beautify(JSON.stringify(json), { indent_size: 2 }));
      }
    });

    this.events.subscribe('misc-components : global-service : add-tab', (tab: tabDataInterface) => {
      let data: componentDataInterface = tab.component.data;
      let newArray = this.tabsArray.find((val) => {
        if (val.component != undefined) {
          return val.component.data.config.id == data.config.id && val.mode == tab.mode;
        }
      });
      if (newArray == undefined) {
        this.tabsArray.push(tab);
        console.log(this.tabsArray);
      }
    });

    this.events.subscribe('page-designer : global-service : add-tab', (tab: tabDataInterface) => {
      let newArray = this.tabsArray.find((val) => {
        if (val.other != undefined) {
          return val.other == tab.other && val.mode == tab.mode;
        }
      });
      if (newArray == undefined) {
        switch (tab.other) {
          case "globalConfigJSON":
            this.tabsArray.push({ mode: "JSON", other: "globalConfigJSON" });
            break;
          case "globalConfigCSS":
            this.tabsArray.push({ mode: "CSS", other: "globalConfigCSS" });
            break;

          default:
            console.log("No tab");
            break;
        }
      }
    });

    this.events.subscribe('page-menu : global-service : add-class', (component: menuListInterface) => {
      addClassFromDump(component, this.global_config.js);
    });
  }

  addCSS(data: componentDataInterface) {
    addCSSFromDump(data);
  }
}



